	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>Imagine if we didn’t judge the author of the code by how few “WTFs” it generated, but instead by how fertile it was\nfor discussion? Or, imagine if every programmer was being judged by what they learned from the discussion. How would\nthat change things? better still, imagine if the “manager's” job was to facilitate the learning and get the\ndiscussion back on track when it strayed into fixing-the-code territory instead of to judge people by how much they\n\"contribute.\" That would be a breath of fresh air to most teams.</p>\n</blockquote>\n<p>An excellent point. Code reviews are valuable for getting eyes on the code, but I agree with Raganwald here that the\nmore valuable outcome is that everyone shares in the learning. I've thought of that as learning <em>about</em> the work that\nothers are doing, but Raganwald makes a good point that you learn from the <em>discussion</em> about the work that others are\ndoing.</p>","fields":{"post":{"title":"Code Reviews","link":"http://raganwald.posterous.com/a-conjecture-about-code-reviews","timestamp":"2012-10-05T00:20Z","date":"October 5, 2012 12:20am UTC"}}}},"pathContext":{"slug":"/code-reviews"}}

